@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

@media only screen and (max-width: 400px) {
  header {
    height: 100px !important;
  }

  main {
    padding-top: 100px !important;
    /* header 50px height*/
  }
}

/* h2 is About */
h2 {
  text-align: left;
  font-family: Verdana, Tahoma;
  letter-spacing: 1.5px;
  border-bottom: 4px solid #dfdfe2;
  max-width: 80%;
  margin-top: 0px;
  padding-top: 10px;
}

h2::before {
  content: "//";
}

.info {
  padding: 0.5px;
}

/* About */

main {
  padding-top: 50px;
  /* header 50px height*/
}

body {
  text-align: center;
  align-items: center;
  background: #1b1b32;
  color: #efefef;
  font-family: Helvetica;
  margin: 0;
}

body a {
  color: #efefef;
  text-decoration: none !important;
}

body a:hover {
  text-decoration: underline;
  background-color: #f1be32;
  color: #1b1b32;
}

.tableOutline {
  width: 90%;
  max-width: 850px;
  max-height: 280px;
  aspect-ratio: 3 / 1;
  margin-left: auto;
  margin-right: auto;
  min-height: 80%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #9bbc0f;
  color: #1b1b32;
  border: 5px outset #272929;
  border-radius: 5px;
}

.fullwidthbar {
  margin: auto;
  height: 1.8rem;
  line-height: 1.8rem;
  background-color: #274717;
  border: 5px outset #272929;
  border-radius: 5px;
  width: calc(70% + 10%);
  /* 10% padding of the below Element 2% padding-left of itself*/
  text-align: left;
  vertical-align: middle;
  font-size: 1.2rem;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  letter-spacing: 0.2rem;
}

.fullwidthbar i {
  margin: 0rem 1rem 0rem 0.5rem;
  background-color: #ffffff;
  padding: 0.2rem;
  width: 1rem;
  height: 1rem;
  line-height: 1rem;
  font-size: 1rem;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
}

#editor {
  min-height: 60%;
  margin-left: auto;
  margin-right: auto;
  padding: 2% 5% 2% 5%;
  border: 5px outset #272929;
  border-radius: 5px;
  resize: both;
  overflow-x: auto;
  overflow-y: scroll;
  color: #1b1b32;
  background: #f8f8f8;
  min-width: 70%;
  max-width: 70%;
  width: max-content;
  display: block;
}

#preview {
  margin-left: auto;
  margin-right: auto;
  padding: 2% 5% 2% 5%;
  background-color: #d2f048;
  color: #1b1b32;
  border: 5px outset #272929;
  border-radius: 5px;
  resize: horizontal;
  overflow-x: auto;
  overflow-y: scroll;
  text-align: left;
  min-width: 70%;
  max-width: 70%;
  width: max-content;
  display: block;
}

#preview h2 {
  border-bottom: none;
}

#preview h2::before {
  content: "";
}

#preview table {
  background-color: #efefef;
  border: 2px solid #1b1b32;
  border-radius: 5px;
}

#preview tr {
  border-top: 1px solid #274717;
  background-color: #efefef;
}

#preview th {
  padding: 6px 13px;
  background: #8d8d8d;
  border: 1px solid #274717;
  text-decoration: underline;
}

#preview td {
  padding: 6px 13px;
  border: 1px solid #274717;
}

#preview table tr:nth-child(2n) {
  background: #cccccc;
}

#preview a {
  color: #003cff;
  text-decoration: underline;
}

#preview blockquote {
  display: block !important;
  color: #030323;
  background: #f1be32;
  font-family: 'Courier New', Courier, monospace;
  padding: 1.5%;
  font-weight: bold;
  border-radius: 5px;
  border-left: 4px solid #272929;
}

#preview code {
  display: inline-block !important;
  color: #efefef;
  background: #272929;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  padding: 1%;
  border-radius: 5px;
}

#copybtn {
  width: 4rem;
  padding: 5px;
  margin: 0px 5px 0px 5px;
  background-color: #2e009a;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  transition-duration: 2s;
  float: right;
}

#copybtn.copied {
  background-color: #f1be32;
}

/*fix empty <p></p> appear at hte end of the markdown*/
p:empty {
  display: none;
}